@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,700;1,500&display=swap");
.calendar {
  padding: 0.5em;
  background-color: #fff; }
  .calendar * {
    font-family: 'Roboto', sans-serif; }
  .calendar-container {
    position: relative;
    width: 270px; }
    .calendar-container.sm {
      font-size: 10px;
      width: 240px; }
    .calendar-container.md {
      font-size: 11.5px;
      width: 270px; }
    .calendar-container.lg {
      font-size: 13px;
      width: 300px; }
  .calendar-navigation {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    font-weight: 500;
    color: #3c4043;
    padding: 0 1em;
    margin: 0.5em 0em; }
    .calendar-navigation > div {
      align-items: center;
      display: flex; }
    .calendar-navigation__year, .calendar-navigation__month {
      font-size: 1.3em; }
    .calendar-navigation__month {
      margin-right: 0.5em; }
    .calendar-navigation__btn {
      background-color: transparent;
      border: none;
      cursor: pointer;
      outline: none;
      display: flex;
      align-items: center;
      transition: background-color 100ms linear;
      border-radius: 50%;
      padding: 0.4em; }
      .calendar-navigation__btn svg {
        color: #3c4043;
        fill: #3c4043; }
      .calendar-navigation__btn span,
      .calendar-navigation__btn svg {
        width: 1em;
        height: 1em; }
      .calendar-navigation__btn:first-child {
        margin-right: 0.5em; }
        .calendar-navigation__btn:first-child svg {
          transform: translateX(-3%); }
      .calendar-navigation__btn:nth-child(2) svg {
        transform: translateX(8%); }
      .calendar-navigation__btn:hover {
        background-color: rgba(32, 33, 36, 0.039); }

.local-days {
  display: flex; }
  .local-days__name {
    padding: 0.5em;
    box-sizing: border-box;
    text-align: center;
    color: #70757a;
    font-size: 1em;
    font-weight: 500;
    min-width: 14.285%;
    width: 14.285%;
    max-width: 14.285%; }

.month-days {
  display: flex;
  flex-wrap: wrap; }
  .month-days__item {
    text-align: center;
    background-color: transparent;
    border: none;
    cursor: pointer;
    border-radius: 50%;
    width: 2.4em;
    height: 2.4em;
    font-size: 1em;
    font-weight: 500;
    transition: background-color 100ms linear;
    outline: none; }
    .month-days__item-container {
      box-sizing: border-box;
      display: flex;
      justify-content: center;
      min-width: 14.285%;
      width: 14.285%;
      max-width: 14.285%; }
    .month-days__item.active {
      background-color: #d2e3fc !important;
      color: #185abc !important; }
    .month-days__item.previous, .month-days__item.next {
      color: #70757a; }
    .month-days__item.current {
      color: #3c4043; }
    .month-days__item:hover {
      background-color: #f1f3f4; }
